.post-container {
    display: grid;
    grid-gap: 20px;grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    /* grid-template-columns: repeat(auto-fit, minmax(75px, 1fr)); */
    /* grid-template-rows: repeat(auto, 50px); */
    align-items: center;
    /* justify-content: space-between; */
  }
  .post {
    background: red;
    border: 3px solid gold;
    text-align: center;
  }

.post-container{
    display: inline-block;
    vertical-align: top;
    margin: 10px;
    
}
.post{
   height: 300px;
   width: 200px;
   border: 1px solid grey;
   text-align: center;
   background-color: rgba(182, 241, 226, 0.75);
   word-wrap: break-word;
  
}
.post div {
    margin: 15px auto;
}
